/* CSS Reset */
*,
*::before,
*::after {
	box-sizing: border-box;
}

* {
	margin: 0;
	padding: 0;
}

body {
	line-height: 1.5;
	-webkit-font-smoothing: antialiased;
}

img,
picture,
video,
canvas,
svg {
	display: block;
	max-width: 100%;
}

input,
button,
textarea,
select {
	font: inherit;
}

p,
h1,
h2,
h3,
h4,
h5,
h6 {
	overflow-wrap: break-word;
}

p {
	text-wrap: pretty;
}
h1,
h2,
h3,
h4,
h5,
h6 {
	text-wrap: balance;
}

#root,
#__next {
	isolation: isolate;
}

/* Styling */
:root {
	font-size: 16px;
	--default-fonts: system-ui, "Segoe UI", Helvetica, Arial, sans-serif,
		"Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
}

body {
	height: 100vh;
	width: 100%;
	display: flex;
	flex-direction: column;
	font-family: "Roboto", "Montserrat", var(--default-fonts);
	color: rgb(242, 243, 244);
}

.background {
	background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
	height: 100vh;
	width: 100%;
	background-size: cover;
	background-position: center center;
	position: fixed;
	top: 0;
	z-index: -1;
	border: none;
}

.blur::after {
	content: "";
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	backdrop-filter: blur(10px);
	z-index: 0;
}

.header {
	display: flex;
	justify-content: center;
	align-items: center;
	padding-top: 32px;
	font-size: 1.7rem;
	font-family: "Montserrat", "Roboto", var(--default-fonts);
}

.header nav {
	display: flex;
	gap: 32px;
}

.header nav > button {
	background: none;
	outline: none;
	border: none;
	color: rgb(242, 243, 244);
	text-shadow: 0 0 10px rgb(0, 0, 0), 0 0 20px rgb(0, 0, 0);
}

button {
	display: flex;
	flex-direction: column;
	align-items: center;
	gap: 8px;
	cursor: pointer;
}

button img {
	width: 30px;
	height: auto;
	filter: drop-shadow(0 0 10px rgb(0, 0, 0));
	transition: transform 0.5s, scale 0.5s;
}

button:hover > img {
	transform: rotate(90deg);
	scale: 1;
}

button > p {
	position: relative;
}

button > p::before {
	content: "";
	position: absolute;
	bottom: 0;
	left: 0;
	width: 0%;
	height: 1px;
	background-color: rgb(214, 189, 152);
	transition: width 0.5s;
}

button:hover > p::before {
	width: 100%;
}

#content {
	flex: 1;
	display: flex;
	flex-direction: column;
	align-items: flex-end;
	justify-content: center;
	height: 100vh;
	gap: 16px;
	transition: opacity 200ms ease-in;
	opacity: 1;
}

.content-title {
	display: flex;
	gap: 12px;
	align-items: flex-end;
	padding-right: 7vw;
	color: rgb(242, 243, 244);
	font-size: 2.5rem;
	font-family: "Montserrat", "Roboto", var(--default-fonts);
	text-shadow: 0 0 10px rgb(0, 0, 0), 0 0 20px rgb(0, 0, 0);
	z-index: 0;
}

.left-title {
    color: rgb(235, 229, 194);
}

.right-title {
	font-weight: 300;
}

.content-description {
	text-align: end;
	width: 35%;
	padding-right: 7vw;
	color: rgb(242, 243, 244);
	font-size: 1.1rem;
	font-weight: 300;
	text-shadow: 0 0 10px rgb(0, 0, 0), 0 0 20px rgb(0, 0, 0);
}

.carousel {
	height: 100%;
	width: 100%;
	position: relative;
}

.carousel > ul {
	list-style: none;
}

.slide {
	height: 100%;
	visibility: hidden;
	position: absolute;
	top: 0;
	left: 0;
	opacity: 0;
	justify-content: space-evenly;
	align-items: center;
	gap: 8px;
	transition: opacity 500ms ease-in-out;
	transition-delay: 500ms;
	z-index: -1;
    overflow: hidden;
}

.slide[data-active] {
	transition-delay: 0ms;
	visibility: visible;
	width: 100%;
	opacity: 1;
	display: flex;
	justify-content: space-evenly;
	align-items: center;
}

.slide[data-active] > img {
	width: calc(550px + 3.5vw);
	height: auto;
	filter: drop-shadow(0 0 20px rgb(0, 0, 0));
}

.slide[data-active] > img[alt*="ramen"] {
	width: calc(500px + 3.5vw);
}

.slide[data-active] > img[alt*="Miso"] {
	width: calc(400px + 3.5vw);
}

.slide[data-active] > span {
	width: 35%;
	padding: 32px 16px;
	color: white;
	background-color: rgba(0, 0, 0, 0.2);
	border-radius: 0.5rem;
	text-align: end;
	text-shadow: 0 0 10px rgb(0, 0, 0), 0 0 20px rgb(0, 0, 0);
}

.slide[data-active] span > h1 {
	font-size: 2.5rem;
	font-family: "Montserrat", "Roboto", var(--default-fonts);
	/* color: rgb(214, 189, 152); */
    color: rgb(235, 229, 194);
	text-transform: uppercase;
}

.slide[data-active] span > h1 > span {
	font-weight: 300;
	color: rgb(242, 243, 244);
}

.slide[data-active] span > p {
	font-weight: 300;
	font-size: 1.1rem;
}

.carousel-button {
	position: absolute;
	top: 50%;
	background: none;
	border: none;
	font-size: 3rem;
	transform: translateY(-50%);
	color: rgba(242, 243, 244, 0.7);
	cursor: pointer;
	border-radius: 0.25rem;
	padding: 0 0.5rem;
	background-color: rgba(0, 0, 0, 0.2);
	transition: color 200ms, background-color 200ms;
}

.carousel-button.prev {
	top: 50%;
	left: 1rem;
	transform: translateY(-50%) rotate(-180deg);
}

.carousel-button.next {
	right: 1rem;
}

.carousel-button:hover,
.carousel-button:focus {
	color: white;
	background-color: rgba(0, 0, 0, 0.3);
}

.carousel-button:focus {
	outline: 1px solid black;
}

.about {
	display: grid;
	grid-template-columns: 1fr 1fr;
	height: 100%;
	width: 100%;
	padding-bottom: calc(24px + 0.5rem);
}

.about .left-section {
	grid-column: 1 / 2;
	background-color: rgb(44, 57, 48);
	border-radius: 10px 0 0 10px;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	gap: calc(8px + 0.5rem);
	text-shadow: 0 0 10px rgb(0, 0, 0);
    margin-top: 5vh;
    margin-left: 10vw
}

.left-section > .story,
.left-section > .secret {
	text-align: center;
	width: calc(250px + 2rem + 3vw);
}

.story > h1,
.secret > h1 {
	font-size: 2.5rem;
	font-family: "Montserrat", "Roboto", var(--default-fonts);
	text-transform: uppercase;
}

.story > span,
.secret > span {
    display: block;
	width: 100%;
	height: 1px;
	background-color: rgb(242, 243, 244);
    margin-bottom: 8px;
}

.story > p,
.secret > p {
	font-family: "Roboto", "Montserrat", var(--default-fonts);
	font-weight: 300;
}

.left-section button {
	color: rgb(242, 243, 244);
	font-family: "Montserrat", "Roboto", var(--default-fonts);
    font-weight: 300;
	background: none;
	outline: none;
	border: 1px solid rgb(242, 243, 244);
	padding: 12px 20px;
	text-transform: uppercase;
	box-shadow: 0 0 10px rgb(0, 0, 0);
	background-color: rgba(0, 0, 0, 0.1);
	transition: scale 200ms;
}

.left-section button:hover {
	color: white;
	background-color: rgba(0, 0, 0, 0.2);
}

.left-section button:active {
    background-color: rgba(0, 0, 0, 0.1);
}

.left-section button:focus {
	outline: 1px solid black;
}

.about .right-section {
	grid-column: 2 / 3;
	background-image: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
	background-position: center center;
	background-repeat: no-repeat;
	background-size: cover;
	border-radius: 0 10px 10px 0;
    margin-top: 5vh;
    margin-right: 10vw;
}
